
// import CouponService from '../services/CouponService';
import CouponService from '../services/CouponService';



export function setFilter(filter) {
    return async dispatch => await dispatch(_setFilter(filter))
}

function _setFilter(filter) {
    return {
        type: 'SET_FILTER',
        filter
    }
}

export function getByTitle(title) {
    // console.log("getByTitle FA ->",title)
    return async dispatch => {
      const coupons = await CouponService.getByTitle(title);
      dispatch({ type: 'GET_BY_TITLE', coupons });
    };
  }
  export default {
    setFilter,
    getByTitle
};